<template lang="pug">
    .test__point 
        .test__question-info
            .test__question
            textarea(type='text' v-model='question.content' rows='3' required).test__question-content
            vue-mathjax(:formula="question.content" v-if='texTranslateVisible').formula__question
        ul.test__answers
            li.test__answer-container(v-for="answer,idx in test.numberAnswers" :key="answer.id")
                new-test-answer-item(
                    :answerChar='answerChars[idx]'
                    :texTranslateVisible='texTranslateVisible'
                    v-on:answerInput='answerInput'
                )
            .correct
                .buttons
                    button(@click.prevent='addNewQuestion()').add__test-item Добавить
                    .latex__on
                        input(type='checkbox' value='latexon' v-model='texTranslateVisible').tex__on
                        span.tex__on-text Включить трансляцию формул
                .correct__answer-content
                    .correct__text Правильный ответ: 
                    select(id='answerChars' name='answerChars' v-model="question.correctAnswer" required).correct__answer
                        option(value=0) Выбрать
                        option(:value='idx+1' v-for='answer,idx in answerChars' v-if='idx<test.numberAnswers') {{answer}}

</template>

<script>
import {VueMathjax} from 'vue-mathjax'
import { mapActions } from "vuex";
export default {
    components: {
        'vue-mathjax': VueMathjax,
        newTestAnswerItem: () => import("./new-test-answer-item")
    },
    props: {
        test: Object,
        texTranslateVisible: Boolean
    },
    data() {
        return {
            question: {
                content: this.formulaQuestion,
                answerA: '',
                answerB: '',
                answerC: '',
                answerD: '',
                answerE: '',
                answerF: '',
                correctAnswer: 0
            },
            answerChars: ['a', 'b', 'c', 'd', 'e', 'f']        
            }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        ...mapActions("testQuestions", ["addTestQuestion"]),
        answerInput(data) {
            switch(data.answerChar){
                case "a":
                this.question.answerA=data.answer
                break
                case 'b':
                this.question.answerB=data.answer
                break
                case 'c':
                this.question.answerC=data.answer
                break
                case 'd':
                this.question.answerD=data.answer
                break
                case 'e':
                this.question.answerE=data.answer
                break
                case 'f':
                this.question.answerF=data.answer
                break
            }
        },
        addNewQuestion() {
            if(this.question.content&&this.question.answerA&&this.question.answerB&&this.question.answerC&&this.question.answerD){
                if(this.test.numberAnswers>5&&this.question.answerE&&this.question.answerF
                || this.test.numberAnswers>4&&this.question.answerE
                || this.test.numberAnswers==4){
                    if(this.question.correctAnswer){
                        let data ={
                        id: this.test.id,
                        question: this.question
                        }
                        this.addTestQuestion(data)
                        this.question = {
                            content: '',
                            correctAnswer: 0
                        }
                        this.$emit('addNewQuestionEvent')
                    }
                }   
            }
        }
    }
}
</script>
